.header {
  width: 100%;

  position: relative;
  background-color: $hero-background;
  overflow: hidden;
}

.header .container {
  padding: 6px 20px 160px;

  @media screen and (min-width: $tablet) {
    padding: 37px 34px 34px;
  }
  @media screen and (min-width: $desktop) {
    padding: 37px 115px 56px;
    max-width: 1200px;
  }
  @media screen and (max-width: $max-mobile) {
    overflow: hidden;
  }
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav {
  display: flex;
  align-items: center;
}

// 
.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  margin-right: 20px;
  font-family: $add-ffamily;
  font-size: 25px;
  line-height: 34px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $primary-title-color;
}
.logo-circle {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 50px;
  background-color: $primary-title-color;
  border-radius: 50%;
  overflow: hidden;
  .logo-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -35%);
    fill: $white-text-color;
  }
}

// NAV

.navigation {
  display: none;
  z-index: 10;
  @media screen and (min-width: $desktop) {
    display: inline-flex;
  }
}
.nav {
  display: inline-flex;

  &-item {
    display: block;

    height: 100%;

    font-size: 16px;
    line-height: 1.3;
    color: $white-text-color;
  }
  &-item:not(:last-child) {
    margin-right: 35px;
  }
  .active {
    color: $primary-title-color;
  }
  &-item:hover,
  &-item:focus {
    color: $primary-title-color;
  }
}

.link {
  height: 100%;
  padding: 20px 0px;
}

//  +  
.menu-burger {
  padding: 0;
  margin-left: auto;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  border: 1px solid transparent;
  @extend %flex-center;
  @media screen and (min-width: $tablet) {
    z-index: 5;
    margin-right: 30px;
  }

  @media screen and (min-width: $desktop) {
    .logo-menu-burger {
      display: none;
    }
  }
}

.header-container .button {
  display: none;
  @media screen and (min-width: $tablet) {
    display: flex;
  }
}
